@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap');

// Reset
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background-color: rgb(13, 36, 56);
    font-family: 'DM Sans', sans-serif;
    color: white;
    margin: 0 50px;

    &::-webkit-scrollbar {
        width: .4rem;
      }
      
      &::-webkit-scrollbar-track {
         background-color: rgb(22, 24, 54);
      }
      
      &::-webkit-scrollbar-thumb {
       
        background: #0f5b91;
          border: 2px solid transparent;
          border-radius: 9px;
          background-clip: content-box;
      }
}

a{
    text-decoration: none;
}

li{
    list-style: none;
}

.text-center{
    text-align: center;
}

.margin-btn{
    margin: 45px 0;
}

//Header CSS
.header{
    height: 12vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 18px;
    z-index: 5;
    position: relative;

    &-left{
        display: flex;
        margin-left: 10px;

       #logo{
          width: 35px;
          height: 37px;
          border-radius: 7px;
          background-color: #d6dbf2;
       }

       #logo-name{
          font-size: 25px;
          font-weight: 400;
          margin-top: 5px;
          margin-left: 7px;
       }
    }

    &-center{
          #hideMeAfter5s{
            font-size: 16px;
            background-color: #3a4456;
            width: 270px;
            position: absolute;           
            top: -5px;
            padding: 0 12px;
            margin-left: 50px;
            border-radius: 10px;
            animation: hideAnimation 0s ease-in 4s 1;
            animation-fill-mode: forwards;
            }
              
            @keyframes hideAnimation {
              to {
                visibility: hidden;
                width: 0;
                height: 0;
               }
            }

          .active{
             color: #1ea6f5;
            }
          
          ul{
            display: flex;

            li{
               padding: 20px;
               font-size: 20px;
               font-weight: 450;
               animation: navintro 1.2s;

               @keyframes navintro{
                 0%{
                    transform: translate(0%, -30%);
                    opacity: 0;
                 }
                 100%{
                    transform: translate(0%, 0%);
                    opacity: 1;
                 }
               }

               a{
                color: white;
                transition: all ease-in .3s;

                &:hover{
                    color: #8bdcec
                }
               }
            }
          }
    }

    &-right{
        display: flex;

        #menu-icon{
            display: none;
            position: absolute;
            width: 31px;
            height: 32px;
            top: 29px;
            right: -25px;
            cursor: pointer;
        }
        }

        #hire-btn{
            padding: 12px 20px;
            border-radius: 8px;
            font-size: 17px;
            font-weight: 550;
            background-color: #6969f1;
            border: none;
            outline: none;
            color: white;
            cursor: pointer;
            transition: all ease-in .3s;

            &:hover{
                background-color: #2626ec;
            }
        }
        .light-icon{  
            width: 35px;
            height: 36px;
            margin-left: 18px;
            background-color: #acacf6de;
            border-radius: 6px;
            margin-top: 5px;
            cursor: pointer;

            svg{
                width: 25px;
                height: 25px;
                margin: 5px 0 0 5px;
            }

            &:hover{
                opacity: 0.8;
            }
        }
    }


//Hero CSS
.hero{
    display: flex;
    height: 95vh;

    &-left{
        flex: 0.35;
        height: 90vh;
        position: relative;

        .hero-text{
            position: absolute;
            bottom: 27vh;
            animation: textintro 1.2s;

            @keyframes textintro{
                0%{
                   transform: translate(-20%, 0%);
                   opacity: 0;
                }
                100%{
                   transform: translate(0%, 0%);
                }
              }

            h1{
                font-size: 2.4vw;
                margin-bottom: 12px;
            }
            h2{
                font-size: 2vw;
                margin-bottom: 35px;
                font-weight: 500;
            }

        .download-btn{
            position: relative;
            padding: 12px 20px;
            border-radius: 8px;
            font-size: 17px;
            font-weight: 200;
            padding-left: 38px;
            background-color: #6969f6;
            border: none;
            outline: none;
            color: white;
            cursor: pointer;
            transition: all ease-in .3s;

            &:hover{
                background-color: #2626ec;
            }
        }
        #down-icon{
            position: absolute;
            width: 22px;
            left: 10px;
        }
    }
}

    &-right{
        flex: 0.65;
        position: relative;

        img{
            width: 850px;
            margin-left: 58px;
            animation: herointro 1.5s;

            @keyframes herointro{
                0%{
                   transform: translate(0%, -20%);
                   opacity: 0;
                }
                100%{
                   transform: translate(0%, 0%);
                }
              }
        }
    }
}

//Projects CSS
.projects{
    margin-top: 70px;
    height: auto;
    
    &-head{

        h1{
            font-size: 38px;
            font-weight: 400;
        }
        p{
            font-size: 20px;
            margin-top: 25px;
            margin-bottom: 25px;
        }
    }

    .search{
        height: 40px;
        display: inline-flex;
        margin-bottom: 6px;
        
        &-icon{
            display: inline-flex;
            align-items: center;
            justify-content: center;
            width: 40px;
            height: 40px;
            background-color: #3a4456;
            border-radius: 10px;
        }
        &-bar{
            width: 200px;
            color: white;
            height: 40px;
            background-color: #3a4456;
            border-radius: 10px;
            border: none;
            margin-left: 8px;
            padding-left: 10px;
            font-size: 16px;
        }
    }

//Card CSS
    &-list{
        margin-bottom: 15px;

        ul{
           display: grid;
           grid-template-columns: repeat(3, 1fr);
           grid-gap: 40px;
           text-align: center;
           margin: 20px 0;

           li{
              height: 55vh;
              width: 25vw;
              border-radius: 10px;
              background-color: #3a4456;
              position: relative;
              transition: all ease-in-out 0.2s;

              &:hover{
                  transform: scale(1.02);
              }

            a {
                padding: 0;
                background-color: transparent;

                &:hover{
                    background-color: transparent;
                }
                
                img{
                width: 25vw;
                height: 44vh;
                object-fit: cover;
                border-top-left-radius: 10px;
                border-top-right-radius: 10px;
            }
            }
            h3{
                margin-top: 11px;
                font-size: 24px;
                font-weight: 400;
            }
            h4{
                font-size: 16px;
                font-weight: 300;
            }
            h5{
                font-size: 14px;
                position: absolute;
                bottom: 87px;
                right: 10px;
                background: black;
                padding: 5px 10px;
                border-radius: 18px;
            }
           }
        }

        .alert-head{
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            height: auto;
            width: 1000px;
            border: 2px solid #ffb902;
            border-radius: 10px;
            padding: 30px 0;
            margin-left: 130px;
            margin-top: 50px;

            .alert{
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;              

                h3{
                    color: red;
                    font-size: 30px;
                    text-shadow: .1px .1px 1px black;
                }

                h4{
                    text-shadow: .1px .1px 2px red;
                    font-size: 19px;
                    margin-top: 8px;
                }
            }
        }
    }

    a{
        position: relative;
        padding: 12px 20px;
        border-radius: 8px;
        font-size: 17px;
        font-weight: 200;
        background-color: #6969f6;
        border: none;
        outline: none;
        color: white;
        cursor: pointer;
        transition: all ease-in .3s;

        &:hover{
            background-color: #2626ec;
        }
    }
}

//Services CSS
.services{
    margin-top: 50px;
    height: auto;
    
    &-head{
        text-align: center;

        h1{
            font-size: 38px;
            font-weight: 400;
        }
        p{
            font-size: 19px;
            margin-top: 20px;
            margin-bottom: 30px;
        }
    }

    &-list{
        ul{
            display: grid;
            grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
            grid-gap: 25px;
            text-align: center;
            margin-top: 50px;

            li{
                height: 300px;
                width: 420px;
                border-radius: 6px;
                background-color: rgb(21, 50, 74);
                transition: all ease-in-out 0.2s;

                &:hover{
                    transform: scale(1.03);
                }

                img{
                    width: 420px;
                    border-radius: 6px 6px 0 0;
                }
                h3{
                    font-size: 22px;
                    font-weight: 500;
                    margin: 10px 0;
                }
            }
        }
    }
}

//Footer CSS
.footer{
    height: auto;

    &-line{
        width: 81.5vw;
        height: 1.5px;
        background-color: #25385c;
        margin-top: 80px;
        margin-left: auto;
        margin-right: auto;
    }
    h1{
        text-align: center;
        margin-top: 80px;
        font-weight: 200;
    }
    ul{
        text-align: center;
        margin-top: 40px;

        a{  
            background-color: #25385c;
            padding: 20px 30px;
            margin: 10px;
            border-radius: 8px;
            position: relative;

            &:hover{
                svg{
                    color: rgb(113, 118, 222);
                }
            }

            i{
                svg{
                    color: rgb(208, 204, 204);
                    display: inline-block;
                    position: absolute;
                    height: 32px;
                    width: 32px;
                    top: 14px;
                    right: 14px;
                }
            }
        }
    }
    .copyright h1{
        font-size: 18px;
        margin: 150px 0 40px 0;

        a{
            cursor: pointer;
            color: white;

            &:hover{
                text-decoration: underline;
            }
        }
    }
}


//About CSS
.about{
    height: 90vh;

    .profile{
        height: 60vh;
        display: flex;

        img{
            width: 280px;
            height: 300px;
            border-radius: 10px;
            object-fit: cover;
            margin-top: 55px;
            margin-left: 30px;
        }
        p{
            margin: 145px 30px 0 90px;
            font-size: 20px;
            line-height: 32px;
        }
    }

    .skill-set{
        height: 28vh;
        background-color: rgb(30 56 81);
        margin: 0 -50px;

        ul{
            display: flex;
            justify-content: space-around;
            align-items: center;

            li{
                margin-top: 40px;

                img{
                    width: 130px;
                    height: 130px;
                    border-radius: 20px;
                }
            }
        }
    }
}

//Contact CSS
.contact{
    height: auto;
    margin-top: 80px;
    display: flex;

    &-form{
        background-color: #112f49;
        height: 100vh;
        width: 570px;
        border-radius: 15px;
        margin: 20px;

        h1{
            font-size: 25px;
            font-weight: 500;
            margin: 40px 0 0 30px; 
        }

        .send-btn{
            padding: 12px 20px;
            border-radius: 8px;
            font-size: 17px;
            font-weight: 200;
            background-color: #6969f6;
            border: none;
            outline: none;
            color: white;
            cursor: pointer;
            transition: all ease-in .3s;
            margin: -40px 0 30px 0;
    
            &:hover{
                background-color: #2626ec;
            }
        }

        .forms{
            margin: 35px;

            label h3{
                font-size: 18px;
                font-weight: 200;
                margin-bottom: 5px;
            }

            input{
                background-color: rgb(30 56 81);
                border: none;
                width: 100%;
                height: 45px;
                margin: 0 2px 20px 2px;
                border-radius: 8px;
                padding-left: 13px;
                font-size: 17px;
                color: white;
                border: 1px solid rgba(50, 74, 74, 0.689);
            }
            #message-input{
                height: 220px;
                
            }
        }
    }

    &-details{
        margin-left: 50px;
        margin-top: 60px;
        width: 400px;

        h1{
            font-size: 25px;
            font-weight: 500;
            margin-bottom: 40px;
        }

        .details{
            display: flex;
            position: relative;
            margin-bottom: 20px;

            svg{
                color: rgb(208, 204, 204);
                display: inline-block;
                position: absolute;
                height: 24px;
                width: 24px;
            }

            h3{
                margin-left: 40px;
                font-size: 18px;
                font-weight: 200;
            }
        }
    }
}

//Popup CSS
.popup{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.559);

    &-content{
        width: 435px;
        height: 630px;
        position: absolute;
        background-color: #112f49;
        border-radius: 10px;
        padding: 10px;
        left: 50%;
        transform: translate(-50%, 10%);

        .popup-header{
            margin-left: 10px;
            padding-top: 5px;

            h1{
               font-size: 24px;
               font-weight: 300;
            }

            .popup-sub-header{
                font-size: 14px;
                font-weight: 200;
                margin-top: 10px;
            }

            .popup-line{
                width: 28.3vw;
                height: 1px;
                margin-left: -20px;
                margin-top: 2px;
                background-color: #25385c;
            }

            img{
                position: absolute;
                right: 15px;
                top: 19px;
                display: flex;
                width: 25px;
                height: 25px;
                cursor: pointer;
                border: .5px solid white;
                border-radius: 40px;
                background-color: white;
                transition: all ease-in .3s;

                &:hover{
                    background-color: #00cec9;
                    border: .5px solid #00cec9;
                }
            }
        }

        .popup-details{
            padding: 25px;
            margin-top: 5px;
            padding-bottom: 12px;

            label h3{
                font-size: 15px;
                font-weight: 200;
                margin-bottom: 6px;
            }

            input{
                background-color: rgb(30 56 81);
                border: none;
                outline: none;
                width: 100%;
                height: 45px;
                margin: 0 2px 20px 2px;
                border-radius: 8px;
                padding-left: 13px;
                font-size: 17px;
                color: white;
                border: 1px solid rgba(50, 74, 74, 0.689);
            }

            #file::-webkit-file-upload-button{
                border: 1px solid #6c5ce7;
                padding: 6px 4px;
                border-radius: 5px;
                background-color: #7a7af3;
                margin: 5px 5px 0 -10px;
                transition: 1s;
                cursor: pointer;
               }
            
            #file::-webkit-file-upload-button:hover{
                background-color: #61c7ec;
                border: 2px solid #00cec9;
             } 
            }

            #project-message{
                height: 180px;
            }
        }

        .buttons{
            
            .request-btn{
                padding: 10px 16px;
                border-radius: 8px;
                font-size: 17px;
                font-weight: 200;
                background-color: #6969f6;
                border: none;                    
                color: white;
                cursor: pointer;
                outline: none;
                transition: all ease-in .3s;
                margin: -20px 104px 30px 0;
            
                &:hover{
                   background-color: #2626ec;
                }
            }
            
            .close-btn{
                padding: 8px 14px;
                border-radius: 8px;
                font-size: 15px;
                font-weight: bold;
                background-color: #ffffff;
                border: none;                    
                color: #ef3939;
                cursor: pointer;
                outline: none;
                transition: all ease-in .3s;
                margin: -40px 0 30px 0;
            
                &:hover{
                   background-color: #f52525;
                   color: white;
                }
            }      
    }
}

.side-bar{
    position: fixed;
    top: 22px;
    right: 6px;
    width: 160px;
    bottom: 58%;
    background-color: #181b30;
    z-index: 5;
    border-radius: 5px;

    .sidebar-content{

        ul{
            height: 20vh;
            text-align: center;
            margin-top: 55px;

            li{
                padding: 25px 20px;
                font-size: 18px;

                &:hover{
                    background-color: #00cec9;
                }
                
                a{
                    color: white;
                }
            }
        }

        img{
            position: absolute;
            height: 22px;
            width: 22px;
            background-color: white;
            border-radius: 20px;
            top: 10px;
            bottom: 0;
            right: 10px;
            cursor: pointer;
        }
    }
}